<script>
import { onMounted, ref, reactive, toRefs, getCurrentInstance, watch } from "vue";
import addico from '../assets/sections/expand.svg'
import delico from '../assets/sections/contract.svg'
export default {
  name: "Home",
  setup() {
    const state = reactive({
      pageModel: {
        current: 1,
        size: 20,
      },
      connect: {
        isConnect: false,
        address: "",
        balance: "",
      },

      totalSupply: 8888,
      mintTotalSupply: 0,
      currentContract: null,
      mintPrice: 0,
      mintNumber: "",
      showModel:{
        tip1:false,
        tip2:false,
        tip3:false,
        tip4:false,
        tip5:false,
        tip6:false,
        tip7:false,
        tip8:false,
        tip9:false,
        tip10:false,
        tip11:false,
        tip12:false,
        tip13:false,
        tip14:false,
        tip15:false,
        tip16:false,
        tip17:false,
        tip118:false,
        tip119:false,
      },
      imgAdd:addico,
      imgDel:delico,
    });
    const { proxy } = getCurrentInstance();

    state.connect = proxy.$store.state.connect;

    // 
    const omitshow = function (value, start, end) {
      if (value != "" && value != null && typeof value != "undefined") {
        var str =
          value.substring(0, start) +
          "..." +
          value.substring(value.length - end);
        return str;
      }
    };
    const connectWallet = async function () {
      const connectResult = await proxy.$utils.CONNECT.linkWallet();
      state.connect.isConnect = connectResult.state;
      if (connectResult.state) {
        state.connect.address = connectResult.address;
        state.connect.balance = connectResult.balance;
        proxy.$store.commit("changeconnect", {
          isConnect: true,
          address: connectResult.address,
          balance: connectResult.balance,
        });
      } else {
        alert(connectResult.msg);
      }
    };
    const disconnect = function () {
      state.connect = {
        isConnect: false,
        address: "",
        balance: "",
      };
      proxy.$store.commit("changeconnect", {
        isConnect: false,
        address: "",
        balance: "",
      });
    };
    //
    const getTotalSupply = async function () {
      let currentContract = proxy.$utils.CONTRACT.getCurrentContract();
      if (currentContract != null) {
        state.currentContract = currentContract;
        state.mintTotalSupply = await proxy.$utils.CONTRACT.totalSupply();
      }
    };
    const getMint = async function () {
      if (state.mintNumber == "" || state.mintNumber == 0) {
        alert("Please enter the correct casting quantity");
        return false;
      }
      if (state.mintNumber > 200) {
        alert("The maximum casting quantity is 200");
        return false;
      }
      if (state.currentContract == null) {
        state.currentContract = proxy.$utils.CONTRACT.getCurrentContract();
      }
      if (state.currentContract != null) {
        let balance = await proxy.$utils.CONNECT.getbalance(
          state.connect.address
        );
        let amount = state.mintNumber * state.mintPrice;

        if (Number(balance) < amount) {
          alert("Insufficient wallet balance");
          console.log(balance, amount);
          return false;
        }
        proxy.$utils.CONTRACT.publicMint(
          state.mintNumber,
          state.mintPrice,
          function (res) {
            alert("Casting success");
            state.mintNumber = "";
            setTimeout(async function () {
              getTotalSupply();
            }, 1000);
          },
          function (err) {
            alert("Casting failed. Please recast later");
          }
        );
      }
    };
    let fapList = ref([
      {title: 'WHAT IS A TVOX?', content: 'A TVOX is a unique avatar with randomly generated traits. TVOX are collectible ERC-721 NFTs. Each TVOX is programmatically generated and provably unique, meaning that no two TVOX will look the same.'},
      {title: 'WHAT IS GALA LABS?', content: 'Gala Labs is a group of creative technologists from around the globe who enjoy exploring, stretching and bringing to life ideas that complement the ecosystem built by Gala Games. We are motivated to find new and exciting ways to creatively use blockchain technology to engage and reward the Gala Games community. We also help brand and IP owners get ready for a decentralized future!'},
      {title: 'WHAT IS AN NFT?', content: 'An NFT is a \'non-fungible token,\' a unique digital asset that can be verified on the blockchain.'},
      {title: 'WHERE ARE TVOX BUILT?', content: 'TVOX are built to live on the TRON blockchain as ERC-721 collectibles, which can be held in an TRON compatible wallet such as Metamask.'},
      {title: 'WILL WALKING DEAD TVOX HAVE UTILITY IN TOWN STAR OR MIRANDUS?', content: 'No, TWD TVOX will not be playable in Town Star or Mirandus.'},
      {title: 'WHAT DOES WALKING DEAD TVOX UTILITY LOOK LIKE IN THE WALKING DEAD: EMPIRES?', content: 'Players will be able to place their owned TVOX within their home bases for passive benefits to gameplay stats for themselves, allies, or both. The rarest TWD TVOX will have more specialized buffs. Exact details of this system are subject to change.'},
      {title: 'DO TVOX HAVE UTILITY?', content: 'Yes, TVOX are designed to have various utilities. That means they will serve a purpose beyond just being a cute avatar that you can own and animate. TVOX will be able to lock up GALA and, eventually earn TVOXcoin as rewards for play. There are additional utilities as well, such as play-to-earn rewards in Town Star and in-game benefits in Mirandus.'},
      {title: 'WHEN DO I PAY GAS FEES?', content: 'You will need to pay gas fees twice. Once when purchasing your TVOX box in the Gala Store and also when you reveal your TVOX by exchanging your box against the contract.'},
      {title: 'IS EACH SERIES UNIQUE?', content: 'Yes. Not only is each TVOX unique, but each series contains unique sets of randomized traits and characteristics, ensuring new styles for each new series.'},
      {title: 'WHAT IS TVOXCOIN?', content: 'TVOXcoin is the coin that will be generated by TVOX. Your TVOXcoin will be usable across the Gala Games metaverse. You may also be able to exchange your TVOXcoin for other coins or currencies.'},
      {title: 'DOES RARITY MATTER?', content: 'Every TVOX is unique, not just because of their metadata but also in the way they look. That said, some TVOX are waaaaay more rare than others. When it comes to TVOXcoin, the rarer your TVOX, the greater your potential rewards.'},
      {title: 'HOW ARE THE TRAITS PROVABLY RANDOMLY GENERATED?', content: 'Each TVOX starts with the selection of a class, and from there, the traits appropriate for that class are iterated, making selections from the allowed options. Data is then compared to all other existing TVOX to ensure uniqueness. Each trait has a list of options, either a color palette (with a selection of colors designed to work together) or a group of specific choices, like hair styles or held items. As different classes have different requirements, some classes have different color palettes or styles, and the generation takes this into consideration. The TVOX sold are picked from a large pool of created TVOX as well - making your TVOX that much more unique.'},
      {title: 'HOW ARE THEY PROVABLY RANDOMLY DISTRIBUTED?', content: 'Embedded in the TVOX contract is a Provenance hash, an SHA-256 calculated from all TVOX metadata, models, and images in sequential order. You can use this to prove that your TVOX hasn\'t been rearranged or manipulated in any way during the pre-reveal period. When enough TVOX have been purchased, and the reveal occurs, a random number will be generated using Chainlink VRF (https://chain.link/solutions/chainlink-vrf). This number is used as an offset to determine the actual TVOX you get! Theres no way to know in advance what TVOX youll get.'},
      {title: 'HOW MANY TVOX DROPS HAVE THERE BEEN?', content: 'The Town Star inaugural series was the first TVOX drop, the Mirandus TVOX series was the second, and now TWD TVOX are the third.'},
      {title: 'WHAT HAPPENS IF I CAN\'T BUY OR I DON\'T GET ONE FAST ENOUGH?', content: 'Gala Labs and its partners are working on several exciting projects, so there will be more opportunities to play with us very soon! Make sure you are on our email list to stay up-to-date.'},
      {title: 'HOW DO I BUY ONE?', content: 'TVOX can be purchased in primary sales (drops) through the Gala Games store. This means all participants must have a Gala Games account, which is free to create and includes a web3 TRON compatible wallet. TRX will be needed to cover network fees (gas) and the purchase price will be payable in GALA.'},
      {title: 'WHY HAVE GALA LABS CREATED TVOX?', content: 'It started because Gala Games has the best community out there - for real. We wanted to create a character series that our community could adopt and use, but a simple avatar just wasnt enough. In Gala fashion, we wanted to go further and do something more than simply repeating a formula. Thats why were bringing TVOX, avatars that de-fi, to the metaverse!'},
      {title: 'HOW MANY TOTAL TVOX WILL THERE BE?', content: 'There will be 8888 TVOX per series.'},
      {title: 'WHAT KIND OF TVOX CAN I BUY?', content: 'You cannot choose your TVOX. Instead, the smart contract will mint you a randomly generated TVOX based on the TVOX programmatic model. The metadata of a TVOX will be cryptographically verifiable based on the smart contract.'},
      {title: 'HOW DO I GET A TVOX AFTER THE PRIMARY SALE ENDS?', content: 'Because they are non-fungible tokens on the TRON blockchain, TVOX can be traded on secondary markets like APENET. There will usually be opportunities to purchase TVOX from other collectors on these markets, but secondary market prices and values are completely unpredictable.'},
      {title: 'WHAT CURRENCIES ARE ACCEPTED FOR PAYMENT?', content: 'Payments will be accepted in GALA only. (Make sure you have enough TRX to cover gas fees.)'},
      {title: 'HOW CAN I ANIMATE MY TVOX?', content: 'When you buy a TVOX, you will get the corresponding FBX file, which can be used to import your TVOX into game engines such as Unity and 3D model viewing programs. This is the gateway to animating, 3D printing and more. Maybe one day, we will run a how to AMA to share the great fun weve had testing FBX demos and playing in Unity!'}
    ]);
    let faqIndex = ref(null)
    const methods = {
      faqShowContent(e, index) {
        faqIndex.value = faqIndex.value === index ? null : index
      }
    };
    onMounted(() => {
      setTimeout(async function () {
        getTotalSupply();
      }, 1000);
    });
    return {
      ...toRefs(state), //  state  state 
      ...methods,
      connectWallet,
      disconnect,
      getTotalSupply,
      omitshow,
      getMint,
      fapList,
      faqIndex
    };
  },
};
</script>



